* {
    padding: 0;
    margin:  0;
}

.App {
    overflow-x: hidden;
    display: grid;
    justify-content: center;
    align-content: start;
    font-family: 'Pacifico', cursive, serif;
    --base: #efd3b5;
    --pink1: #5f093d;
    --pink2: #b21368;
    --pink3: #d67ba8;
    background-color: var(--base);
    width: 100%;
    min-height: 100vh;
}

h1 {
    color: var(--pink1);
}

h1#title {
    font-size: 2.7rem;
    padding-top: 2rem;
}

#links {
    margin-top: 2rem;
    display: grid;
    grid-gap: 0.5rem;
    justify-items: center;
}

.Flourish {
    height: 4rem;
    margin: 1rem;
    fill: var(--pink1);
}

.Flourish:nth-of-type(2n) {
    transform: scaleY(-1.0);
}

#links a {
    font-family: "Readex Pro", sans-serif;
    font-size: 2rem;
    color: var(--pink1);
    text-decoration: none;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
